.description,
.title {
  align-self: stretch;
  position: relative;
  line-height: 150%;
}
.description {
  font-size: var(--font-size-sm);
}
.intro {
  align-self: stretch;
  gap: var(--gap-base);
}
.column,
.content,
.intro {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  padding: var(--padding-5xl) 0;
  gap: var(--gap-5xl);
}
.column {
  flex: 1;
}
.imageIcon {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.description1 {
  flex: 1;
  position: relative;
  line-height: 150%;
}
.column1,
.feature {
  display: flex;
  justify-content: flex-start;
}
.feature {
  align-self: stretch;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-5xl);
}
.column1 {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-base);
  font-size: var(--font-size-sm);
}
.columns,
.featuresListModified {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.columns {
  width: 960px;
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
.featuresListModified {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  overflow: hidden;
  padding: var(--padding-45xl) var(--padding-5xl);
  box-sizing: border-box;
}
@media screen and (max-width: 690px) {
  .column,
  .column1 {
    flex: unset;
    align-self: stretch;
  }
  .columns {
    flex-direction: column;
  }
}
